// This code is copied from https://github.com/hguo/WebGL-tutorial/blob/master/shaders_setup.js, the WebGL sample code
// provided by Hanqi Guo.
function getShader(gl: WebGL2RenderingContext, type: number, source: string): WebGLShader {
    let shader: WebGLShader = gl.createShader(type);

    gl.shaderSource(shader, source);
    gl.compileShader(shader);

    if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
        alert(gl.getShaderInfoLog(shader));
        return null;
    }

    return shader;
}

function initShaderProgram(gl: WebGL2RenderingContext, fragmentShaderId: string, vertexShaderId: string): WebGLProgram {
    let shaderProgram = gl.createProgram();

    let fragmentSource: string = require("./shaders/fragment.glsl");
    let vertexSource: string = require("./shaders/vertex.glsl");

    let fragmentShader: WebGLShader = getShader(gl, gl.FRAGMENT_SHADER, fragmentSource);
    let vertexShader: WebGLShader = getShader(gl, gl.VERTEX_SHADER, vertexSource);

    gl.attachShader(shaderProgram, vertexShader);
    gl.attachShader(shaderProgram, fragmentShader);
    gl.linkProgram(shaderProgram);

    if (!gl.getProgramParameter(shaderProgram, gl.LINK_STATUS)) {
        alert("Could not initialise shaders");
    }

    gl.useProgram(shaderProgram);

    return shaderProgram;
}

export { initShaderProgram }