import { Constants } from "./constants";
import { Entity } from "./entity";
import { RenderState } from "./render_state";

abstract class Component {
    public update(delta: number, entity: Entity): void {
    }

    public render(gl: WebGL2RenderingContext, renderState: RenderState, entity: Entity): void {
    }
}

class Primitive extends Component {
    private glDrawMode: number;
    private vertexBuffer: WebGLBuffer;
    private colorBuffer: WebGLBuffer;
    private indexBuffer: WebGLBuffer;
    private vertexCount: number;

    private constructor(drawMode: number, vertexBuffer: WebGLBuffer, colorBuffer: WebGLBuffer, indexBuffer: WebGLBuffer,
        vertexCount: number) {
        super();
        this.glDrawMode = drawMode;
        this.vertexBuffer = vertexBuffer;
        this.colorBuffer = colorBuffer;
        this.indexBuffer = indexBuffer;
        this.vertexCount = vertexCount;
    }

    public static create(gl: WebGL2RenderingContext, vertices: Array<number>, colors: Array<number>,
        indices: Array<number>): Primitive {
        if (vertices.length / Constants.FLOATS_PER_VERTEX !== colors.length / Constants.FLOATS_PER_COLOR) {
            console.error("Number of vertices not equal to number of colors");
            return null;
        }

        // Assemble the vertex buffer
        const vertexBuffer: WebGLBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, vertexBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(vertices), gl.STATIC_DRAW);

        // Assemble the color buffer
        const colorBuffer: WebGLBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, colorBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(colors), gl.STATIC_DRAW);

        // Assemble the index buffer
        const indexBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indexBuffer);
        gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, new Uint16Array(indices), gl.STATIC_DRAW);

        let component: Primitive
            = new Primitive(gl.TRIANGLES, vertexBuffer, colorBuffer, indexBuffer, indices.length);
        return  component;
    }

    public render(gl: WebGL2RenderingContext, renderState: RenderState, entity: Entity): void {
        // Set the position attribute
        gl.bindBuffer(gl.ARRAY_BUFFER, this.vertexBuffer);
        gl.vertexAttribPointer(renderState.vertexPositionAttribute, Constants.FLOATS_PER_VERTEX, gl.FLOAT, false, 0, 0);
        gl.enableVertexAttribArray(renderState.vertexPositionAttribute);

        // Set the color attribute
        gl.bindBuffer(gl.ARRAY_BUFFER, this.colorBuffer);
        gl.vertexAttribPointer(renderState.vertexColorAttribute, Constants.FLOATS_PER_COLOR, gl.FLOAT, false, 0, 0);
        gl.enableVertexAttribArray(renderState.vertexColorAttribute);

        // Bind the index buffer (isn't associated with an attribute)
        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.indexBuffer);

        // Set any uniforms associated with this object
        gl.uniformMatrix4fv(renderState.uniformModelMatrix, false, entity.transformation);

        // Draw the object
        gl.drawElements(this.glDrawMode, this.vertexCount, gl.UNSIGNED_SHORT, 0)
    }
}

class Spinner extends Component {
    private speed: number;
    
    public constructor(speed: number) {
        super();
        this.speed = speed;
    }

    public update(delta: number, entity: Entity): void {
        let scaledSpeed: number = this.speed * delta;
        entity.rotation.x += scaledSpeed;
        entity.rotation.y += scaledSpeed;
        entity.rotation.z += scaledSpeed;
    }
}

export { Component, Primitive, Spinner };
