import { Camera } from "./camera";
import { initShaderProgram } from "./shader_setup";

class RenderState {
    // Shader attributes
    public readonly vertexPositionAttribute: number;
    public readonly vertexColorAttribute: number;
    
    // Uniform locations
    public readonly uniformModelMatrix: WebGLUniformLocation;
    private uniformViewProjectionMatrix: WebGLUniformLocation;
    
    // Internal state
    public readonly camera: Camera;
    private shaderProgram: WebGLProgram;

    private constructor(gl: WebGL2RenderingContext, camera: Camera) {
        this.shaderProgram = initShaderProgram(gl, "shader-fs", "shader-vs");
        this.camera = camera;

        // Initialize vertex attributes
        this.vertexPositionAttribute = gl.getAttribLocation(this.shaderProgram, "aVertexPosition");
        gl.enableVertexAttribArray(this.vertexPositionAttribute); 
        this.vertexColorAttribute = gl.getAttribLocation(this.shaderProgram, "aVertexColor");
        gl.enableVertexAttribArray(this.vertexColorAttribute);
        
        // Initialize the shader uniforms
        this.uniformModelMatrix = gl.getUniformLocation(this.shaderProgram, "uModelMatrix");
        this.uniformViewProjectionMatrix = gl.getUniformLocation(this.shaderProgram, "uViewProjectionMatrix");
    
        // Set the clear color
        gl.clearColor(0, 0, 0, 1);
    }

    public static create(gl: WebGL2RenderingContext) {
        let camera: Camera = Camera.create(gl);

        return new RenderState(gl, camera);
    }

    public Begin(gl: WebGL2RenderingContext): void {
        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
        gl.clearDepth(1.0);
        gl.enable(gl.DEPTH_TEST);
        gl.depthFunc(gl.LEQUAL);
    
        // Set the shader uniforms
        gl.useProgram(this.shaderProgram);
        gl.uniformMatrix4fv(this.uniformViewProjectionMatrix, false, this.camera.viewProjection);
    }
}

export { RenderState }