import { Matrix4 } from "./matrix4";
import { Quaternion } from "./quaternion";
import { Vector3 } from "./vector3";

/**
 * An object that exists within a 3D space.
 */
abstract class SpatialObject {
    public rotation: Vector3;
    public translation: Vector3;
    public scale: Vector3;

    private parentObj: SpatialObject;

    public constructor() {
        this.rotation = Vector3.zero;
        this.translation = Vector3.zero;
        this.scale = Vector3.one;
    }

    public translate(a: Vector3): void {
        this.translation.add(a);
    }

    public get transformation(): Matrix4 {
        if (this.parentObj == null) {
            return Matrix4.fromRotationTranslationScale(
                Quaternion.fromEuler(this.rotation.x, this.rotation.y, this.rotation.z), this.translation, this.scale);
        } else {
            return Matrix4.multiply(this.parentObj.transformation, Matrix4.fromRotationTranslationScale(
                Quaternion.fromEuler(this.rotation.x, this.rotation.y, this.rotation.z), this.translation, this.scale));
        }
    }

    public set parent(parentObj: SpatialObject) {
        this.parentObj = parentObj;
    }
}

export { SpatialObject };

