import { Camera } from "./camera";
import { CameraController } from "./camera_controller";
import { Entity } from "./entity";
import { EntityFactory } from "./entity_factory";
import { RenderState } from "./render_state";
import { Vector3 } from "./vector3";

class World {
    private entities: Array<Entity> = [];

    private constructor(entities: Array<Entity> = []) {
        this.entities = entities;
    }

    public static create(gl: WebGL2RenderingContext, camera: Camera): World {
        camera.addComponent(new CameraController());
        let spinningCubes: Array<Entity> = new Array<Entity>(15);
        let spinningCube1: Entity = EntityFactory.createSpinningCube(gl);
        spinningCube1.translation = Vector3.multiply(Vector3.forward, -6);
        console.log(spinningCube1.translation);
        spinningCubes[0] = spinningCube1;

        let scale: number = 0.5;

        for (let i = 1; i < spinningCubes.length; i++) {
            scale -= 0.01;
            let spinningCube: Entity = EntityFactory.createSpinningCube(gl);
            spinningCube.parent = spinningCubes[Math.round(Math.random() * i) - 1];
            spinningCube.translation = Vector3.random((Math.random() - 0.5) * 50);
            spinningCube.scale = Vector3.multiply(Vector3.one, scale);
            spinningCubes[i] = spinningCube;
        }

        return new World(spinningCubes.concat([
            camera
        ]));
    }

    public update(delta: number): void {
        this.entities.forEach((entity) => {
            entity.update(delta);
        });
    }

    public render(gl: WebGL2RenderingContext, renderState: RenderState): void {
        this.entities.forEach((entity) => {
            entity.render(gl, renderState);
        });
    }

    public addEntity(entities: Entity): void {
        this.entities.push(entities);
    }

    public addEntities(entities: Array<Entity>): void {
        entities.forEach((entity) => {
            this.addEntity(entity);
        });
    }
}

export { World };

