class Vector3 extends Float32Array {
    public constructor(x: number = 0, y: number = 0, z: number = 0) {
        super(3);
        this[0] = x;
        this[1] = y;
        this[2] = z;
    }

    public static add(a: Vector3, b: Vector3): Vector3 {
        return new Vector3(a.x + b.x, a.y + b.y, a.z + b.z);
    }

    public static multiply(a: Vector3, b: number): Vector3 {
        return new Vector3(a.x * b, a.y * b, a.z * b);
    }

    public add(a: Vector3): void {
        this.x += a.x;
        this.y += a.y;
        this.z += a.z;
    }

    public multiply(a: number): void {
        this.x *= a;
        this.y *= a;
        this.z *= a;
    }

    public normalize(): void {
        let x = this[0];
        let y = this[1];
        let z = this[2];
        let len = x * x + y * y + z * z;
        if (len > 0) {
          //TODO: evaluate use of glm_invsqrt here?
          len = 1 / Math.sqrt(len);
        }
        this[0] = this[0] * len;
        this[1] = this[1] * len;
        this[2] = this[2] * len;
    }

    public subtract(a: Vector3): void {
        this.x -= a.x;
        this.y -= a.y;
        this.z -= a.z;
    }

    public static get zero(): Vector3 {
        return new Vector3(0, 0, 0);
    }

    public static get one(): Vector3 {
        return new Vector3(1, 1, 1);
    }

    /**
     * (1, 0, 0)
     */
    public static get right(): Vector3 {
        return new Vector3(1, 0, 0);
    }

    /**
     * (0, 1, 0)
     */
    public static get up(): Vector3 {
        return new Vector3(0, 1, 0);
    }

    /**
     * (0, 0, 1)
     */
    public static get forward(): Vector3 {
        return new Vector3(0, 0, 1);
    }

    /**
     * (0 to 1, 0 to 1, 0 to 1) * scale
     */
    public static random(scale: number = 1): Vector3 {
        return new Vector3(Math.random() * scale, Math.random() * scale, Math.random() * scale);
    }

    public get x(): number {
        return this[0];
    }

    public set x(dx: number) {
        this[0] = dx;
    }

    public get y(): number {
        return this[1];
    }

    public set y(dy: number) {
        this[1] = dy;
    }

    public get z(): number {
        return this[2];
    }

    public set z(dz: number) {
        this[2] = dz;
    }
}

export { Vector3 }