import { ComponentFactory, PrimitiveType } from "./component_factory";
import { Entity } from "./entity";

namespace EntityFactory {
    export function createSpinningCube(gl: WebGL2RenderingContext): Entity {
        let spinner: Entity = new Entity([
            ComponentFactory.createSpinner(45),
            ComponentFactory.createGLElement(gl, PrimitiveType.Cube)
        ]);
        return spinner;
    }
}

export { EntityFactory };

