import { Input } from "./input";
import { RenderState } from "./render_state";
import { World } from "./world";
import "./styles/stylesheet.css"

// WebGL context
let gl: WebGL2RenderingContext;

// Globals
let world: World;
let renderState: RenderState;
let previousTime = 0;

// HTML elements
let canvas: HTMLCanvasElement = document.getElementById("code00-canvas") as HTMLCanvasElement;

main();

/**
 * Initialize WebGL.
 * @param canvas the canvas attached to the WebGL context
 */
function initGL(canvas: HTMLCanvasElement): WebGL2RenderingContext {
    let gl: WebGL2RenderingContext;

    try {
        gl = canvas.getContext("webgl2") as WebGL2RenderingContext;  // the graphics context
    } catch (e) {
        gl = null;
        console.error("Couldn't load the WebGL context");
    }

    return gl;
}

/**
 * Update the scene.
 * @param delta The time, in seconds, since the last call to update.
 */
function update(delta: number) {
    world.update(delta);
}

/**
 * Render the scene, including all game objects in the world.
 */
function render(): void {
    renderState.Begin(gl);
    world.render(gl, renderState);
}

/**
 * Process a single animation frame.
 * @param time The number of milliseconds since the first call to process.
 */
function tick(time: DOMHighResTimeStamp): void{
    // Calculate delta time
    let delta: number = (time - previousTime) * 0.001;
    previousTime = time;

    // Update and draw
    update(delta);
    render();
    
    // Loop
    requestAnimationFrame(tick);
}

/**
 * Start WebGL.
 */
function main(): void {
    gl = initGL(canvas);
    
    if (!gl) {
        alert("Could not initialise WebGL, sorry :-(");
        return;
    }

    // Initialize systems
    Input.initialize(require("./config/input.json"));
    renderState = RenderState.create(gl);
    world = World.create(gl, renderState.camera);

    // Start the loop
    requestAnimationFrame(tick);
}